import React, { FunctionComponent } from "react";
import { lazy } from "react";
import { withTranslation, WithTranslation } from "react-i18next";

const Container = lazy(() => import("../common/Container"));
const ContentBlock = lazy(() => import("../components/ContentBlock"));

const Tokenomics: FunctionComponent<WithTranslation> = ({ t }) => {
  return (
    <Container>
      <ContentBlock
        type="right"
        title={t("introductionTitle")}
        content={t("introductionText")}
        icon={`./img/svg/introtoken.svg`}
        id="intro"
      />
      <ContentBlock
        type="left"
        title={t("tokenTitle")}
        content={t("tokenText")}
        icon={`./img/svg/token.svg`}
        id="token"
      />
      <ContentBlock
        type="right"
        title={t("distributionTitle")}
        content={t("distributionText")}
        icon={`./img/svg/distribution.svg`}
        id="distribution"
      />
      <ContentBlock
        type="left"
        title={t("utilityTitle")}
        content={t("utilityText")}
        icon={`./img/svg/utility.svg`}
        id="utility"
      />
    </Container>
  );
};

export default withTranslation()(Tokenomics);
